.login-pages {
    position: relative;
    &__inner {
        position: relative;
        .cover-login {
            display: block;
            position: fixed;
            height: 100%;
            width: 50vw;
            top: 0;
            background-size: 100% !important;
            .pintar-logos {
                position: absolute;
                height: 100%;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                .logo-png {
                    width: 100%;
                    max-width: 300px;
                    text-align: center;
                    img.icn-logo {
                        width: 100%;
                        max-width: 200px;
                        margin-bottom: 15px;
                    }
                    img.txt-logo {
                        width: 100%;
                        max-width: 150px;
                        display: block;
                        margin: 0 auto;
                    }
                }
            }
        }
        .login-form {
            position: fixed;
            top: 0;
            width: 50vw;
            left: 50vw;
            &__inner {
                display: flex;
                align-items: center;
                justify-content: center;
                height: 100vh;
                width: 100%;
                .login-card {
                    position: relative;
                    padding: 60px;
                    h1 {
                        font-size: 38px;
                        font-weight: bold;
                        color: #000;
                    }
                    p {
                        font-size: 14px;
                        margin-bottom: 80px;
                        font-weight: 500;
                        opacity: 0.6;
                    }
                    button {
                        display: block;
                        width: 50%;
                        float: right;
                        background: #299a4a;
                        color: #fff;
                        border: none;
                        padding: 8px;
                        font-family: sarabun;
                        font-weight: bold;
                        font-size: 14px;
                    }
                    a {
                        color: #299a4a;
                        text-decoration: none !important;
                        font-weight: 600;
                        font-size: 12px;
                        margin-top: 10px;
                        display: block;
                    }
                }
                .grp-form {
                    margin-bottom: 15px;
                    input {
                        background: transparent;
                        border: none;
                        border-bottom: solid 1px #b1b1b1;
                        width: 350px;
                        font-size: 16px;
                        padding: 10px 0px;
                        font-weight: 600;
                        font-family: sarabun;
                    }
                    label {
                        display: block;
                        font-weight: 500;
                        font-size: 12px;
                    }
                }
                .term__box {
                    margin-top: 84px;
                    font-size: 12px;
                    font-weight: 500;
                    .trm-link {
                        a {
                            display: inline-block !important;
                        }
                    }
                }                
            }
        }
    }
}